                      <td v-bind:class="this.pClass(riscos.comCom)">{{ riscos.comCom * 100 }} %</td>
                    </tr>
                    <tr>
                      <td>A😷⇐😒B</td>
                      <td v-bind:class="this.pClass(riscos.comSem)">{{ riscos.comSem * 100 }} %</td>
                    </tr>
                    <tr>
                      <td>A😒⇐😷B</td>
                      <td v-bind:class="this.pClass(riscos.semCom)">{{ riscos.semCom * 100 }} %</td>
                    </tr>
                    <tr>
                      <td>A😒⇐😒B</td>
                      <td v-bind:class="this.pClass(riscos.semSem)">{{ riscos.semSem * 100 }} %</td>
                    </tr>
                  </tbody>
                </table>                
                <p class="is-size-7">Fonte: xxx</p>
                
                <br>
                
                <p>
                  A probabilidades do indivíduo "A" ser contaminado é a somatória das probabilidades multiplicada pela chance do indivíduo "B" estar contaminado.
                </p>
                <br>
                
                <p v-if="!verTabelaCompleta">O descritivo de cálculos para os casos extremos é apresentado abaixo:</p>
                <p v-if="verTabelaCompleta">Todas as situações da simulação são apresentadas na tabela abaixo:</p>

                <button v-if="!verTabelaCompleta" class="button is-small" @click="verTabelaCompleta = true">Ver tabela completa</button>
                <button v-if="verTabelaCompleta" class="button is-small" @click="verTabelaCompleta = false">Recolher tabela</button>
                
