                        <span>
                          
                        </span>
                        <span v-if="item[2].length > 1">
                          (<span v-bind:class="item[4][j]" v-for="(p, j) in item[2]" v-bind:key="j">{{ p }}<span class="has-text-white">{{ j !== item[2].length - 1 ? ' + ' : '' }}</span>
                        </span>) × </span>
                        
                        <span class="has-text-info-dark">{{probEstarContaminado}}</span>
                    </td>
                    <td><span>{{ item[3] === 0 ? '&lt; 0.0001' : item[3] }} %</span></td>
                    </tr>
                  </tbody>
                </table>
                <button v-if="verTabelaCompleta" class="button is-small" @click="verTabelaCompleta = false">Recolher tabela</button>



                

                <br>
                <!-- <p>passageiro está com o distanciamento inferior a 1,5 m com outros {{ violacoesDeDistanciamentoMedia }} passageiros em média.</p> -->
<!--                 <p class="is-size-5">Para o caso simulado, ocorreram as seguintes eventos:</p>

                <table class="table">
                  <thead>
                    <tr>
